@import url('https://fonts.googleapis.com/css2?family=Bangers&family=Permanent+Marker&family=Space+Grotesk:wght@400;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Space Grotesk', sans-serif;
  overflow-x: hidden;
  background: #000;
}

.spiderverse-portfolio {
  position: relative;
  background: #000;
  color: #fff;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

/* Story Section - Full Screen */
.story-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  scroll-snap-align: start;
  padding: 2rem;
}

html {
  scroll-behavior: smooth;
}

/* Halftone Background */
.halftone-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle, rgba(255, 0, 68, 0.15) 1px, transparent 1px),
    radial-gradient(circle, rgba(0, 153, 255, 0.15) 1px, transparent 1px);
  background-size: 8px 8px, 12px 12px;
  background-position: 0 0, 4px 4px;
  pointer-events: none;
  z-index: 0;
  animation: halftoneShift 20s linear infinite;
}

@keyframes halftoneShift {
  0% { transform: translate(0, 0); }
  100% { transform: translate(20px, 20px); }
}

/* Floating SFX */
.floating-sfx {
  position: fixed;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 100;
}

.sfx-text {
  position: absolute;
  font-family: 'Bangers', cursive;
  font-size: 3rem;
  color: #FFD700;
  text-shadow: 
    3px 3px 0 #000,
    -3px -3px 0 #000,
    3px -3px 0 #000,
    -3px 3px 0 #000;
  opacity: 0;
  animation: vibrate 0.3s infinite, fadeInOut 4s infinite;
}

.sfx-thwip {
  top: 10%;
  left: 80%;
  animation-delay: 0s;
  transform: rotate(-15deg);
}

.sfx-boom {
  top: 60%;
  left: 10%;
  animation-delay: 2s;
  transform: rotate(10deg);
  color: #FF0044;
}

.sfx-wow {
  top: 30%;
  left: 15%;
  animation-delay: 4s;
  transform: rotate(-8deg);
  color: #00E5FF;
}

@keyframes vibrate {
  0%, 100% { transform: translate(0, 0) rotate(var(--rotate, 0deg)); }
  25% { transform: translate(-2px, 2px) rotate(var(--rotate, 0deg)); }
  50% { transform: translate(2px, -2px) rotate(var(--rotate, 0deg)); }
  75% { transform: translate(-2px, -2px) rotate(var(--rotate, 0deg)); }
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0; }
  10%, 90% { opacity: 0.8; }
}

/* Hero Section */
.hero-section {
  position: relative;
  z-index: 10;
  background: 
    linear-gradient(135deg, rgba(255, 0, 68, 0.1) 0%, rgba(0, 153, 255, 0.1) 100%),
    radial-gradient(circle at 30% 50%, rgba(255, 0, 68, 0.2) 0%, transparent 50%),
    radial-gradient(circle at 70% 50%, rgba(0, 153, 255, 0.2) 0%, transparent 50%);
}

.hero-content {
  position: relative;
  text-align: center;
  z-index: 2;
  width: 100%;
  max-width: 1200px;
  padding: 0 1rem;
}

.comic-burst {
  position: absolute;
  top: -100px;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 300px;
  background: 
    radial-gradient(circle, rgba(255, 215, 0, 0.3) 0%, transparent 70%);
  filter: blur(40px);
  animation: pulseBurst 2s ease-in-out infinite;
}

@keyframes pulseBurst {
  0%, 100% { transform: translateX(-50%) scale(1); opacity: 0.5; }
  50% { transform: translateX(-50%) scale(1.2); opacity: 0.8; }
}

.glitch-title {
  font-family: 'Bangers', cursive;
  font-size: clamp(3rem, 10vw, 8rem);
  color: #FF0044;
  text-shadow:
    5px 5px 0 #00E5FF,
    -5px -5px 0 #FFD700,
    10px 10px 20px rgba(255, 0, 68, 0.5);
  letter-spacing: 0.1em;
  margin-bottom: 1rem;
  position: relative;
  animation: glitchAnim 5s infinite;
}

@keyframes glitchAnim {
  0%, 100% { text-shadow: 5px 5px 0 #00E5FF, -5px -5px 0 #FFD700; }
  25% { text-shadow: -5px 5px 0 #00E5FF, 5px -5px 0 #FFD700; }
  50% { text-shadow: 5px -5px 0 #00E5FF, -5px 5px 0 #FFD700; }
  75% { text-shadow: -5px -5px 0 #00E5FF, 5px 5px 0 #FFD700; }
}

.subtitle-box {
  display: inline-block;
  background: #000;
  border: 4px solid #FF0044;
  padding: 1rem 2rem;
  transform: skew(-2deg);
  margin: 1rem 0;
  box-shadow: 
    0 0 20px rgba(255, 0, 68, 0.5),
    inset 0 0 20px rgba(255, 0, 68, 0.2);
}

.role-text {
  font-family: 'Bangers', cursive;
  font-size: clamp(1.2rem, 3vw, 2rem);
  color: #FFD700;
  letter-spacing: 0.15em;
  transform: skew(2deg);
}

.comic-caption {
  max-width: 600px;
  margin: 2rem auto;
  padding: 1.5rem;
  background: rgba(0, 0, 0, 0.8);
  border: 3px solid #00E5FF;
  border-radius: 20px;
  position: relative;
  font-size: 1.1rem;
  line-height: 1.6;
}

.comic-caption::before {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #00E5FF;
}

/* Profile Section */
.profile-section {
  position: relative;
  z-index: 10;
}

.profile-card {
  position: relative;
  width: 400px;
  height: 500px;
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.profile-card:hover {
  transform: scale(1.05) rotateY(5deg) rotateX(-5deg);
}

.card-frame {
  position: relative;
  width: 100%;
  height: 100%;
  background: #000;
  border: 6px solid #FF0044;
  box-shadow:
    0 0 30px rgba(255, 0, 68, 0.6),
    inset 0 0 30px rgba(0, 229, 255, 0.3),
    10px 10px 0 #00E5FF,
    -10px -10px 0 #FFD700;
  overflow: hidden;
  transform: rotate(-2deg);
}

.profile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: 
    contrast(1.2) 
    brightness(1.1)
    saturate(1.3);
  mix-blend-mode: screen;
}

.card-glow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(45deg, 
      rgba(255, 0, 68, 0.3) 0%, 
      transparent 50%, 
      rgba(0, 229, 255, 0.3) 100%);
  pointer-events: none;
  animation: glowPulse 3s ease-in-out infinite;
}

@keyframes glowPulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.7; }
}

.profile-label {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%) rotate(2deg);
  background: #FFD700;
  color: #000;
  padding: 0.5rem 2rem;
  font-family: 'Bangers', cursive;
  font-size: 1.5rem;
  border: 4px solid #000;
  box-shadow: 5px 5px 0 #FF0044;
}

/* Sections */
section {
  position: relative;
  z-index: 10;
}

.panel {
  opacity: 1;
  transform: translateY(0) rotate(0deg);
}

.section-title {
  font-family: 'Bangers', cursive;
  font-size: clamp(2.5rem, 6vw, 5rem);
  text-align: center;
  margin: 3rem 0;
  color: #fff;
  text-shadow: 
    4px 4px 0 #FF0044,
    -4px -4px 0 #00E5FF;
}

.title-impact {
  color: #FFD700;
  display: inline-block;
  animation: bounce 1s ease-in-out infinite;
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

/* About Section */
.about-section {
  position: relative;
  z-index: 10;
}

.narration-box {
  max-width: 800px;
  width: 100%;
  background: rgba(0, 0, 0, 0.9);
  border: 5px solid #FFD700;
  padding: 2rem;
  position: relative;
  box-shadow: 
    0 0 30px rgba(255, 215, 0, 0.4),
    inset 0 0 20px rgba(255, 215, 0, 0.1);
  margin: 0 auto;
}

.narration-header {
  font-family: 'Permanent Marker', cursive;
  font-size: 1.8rem;
  color: #FFD700;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.narration-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #fff;
}

/* Skills Section */
.skills-section {
  position: relative;
  z-index: 10;
  width: 100%;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1.5rem;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
}

.skill-bubble {
  background: #000;
  border: 4px solid #00E5FF;
  border-radius: 50%;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  font-family: 'Permanent Marker', cursive;
  font-size: 1rem;
  color: #00E5FF;
  text-align: center;
  box-shadow: 
    0 0 20px rgba(0, 229, 255, 0.4),
    inset 0 0 20px rgba(0, 229, 255, 0.1);
  transform: rotate(-5deg);
  transition: transform 0.3s, box-shadow 0.3s, border-color 0.3s;
}

.skill-bubble:hover {
  transform: rotate(5deg) scale(1.1);
  border-color: #FFD700;
  color: #FFD700;
  box-shadow: 
    0 0 30px rgba(255, 215, 0, 0.6),
    inset 0 0 20px rgba(255, 215, 0, 0.2);
}

.skill-bubble:nth-child(even) {
  transform: rotate(5deg);
}

.skill-bubble:nth-child(even):hover {
  transform: rotate(-5deg) scale(1.1);
}

/* Experience Section */
.experience-section {
  position: relative;
  z-index: 10;
  width: 100%;
  overflow-y: auto;
}

.timeline {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 2rem;
}

.comic-issue {
  position: relative;
  background: #000;
  border: 6px solid #FF0044;
  padding: 2rem;
  transform: rotate(-1deg);
  box-shadow: 
    0 0 30px rgba(255, 0, 68, 0.4),
    10px 10px 0 rgba(255, 215, 0, 0.5);
  transition: transform 0.3s, box-shadow 0.3s;
}

.comic-issue:hover {
  transform: rotate(1deg) scale(1.02);
  box-shadow: 
    0 0 40px rgba(255, 0, 68, 0.6),
    15px 15px 0 rgba(255, 215, 0, 0.7);
}

.comic-issue:nth-child(even) {
  transform: rotate(1deg);
  border-color: #00E5FF;
}

.comic-issue:nth-child(even):hover {
  transform: rotate(-1deg) scale(1.02);
}

.issue-cover {
  margin-bottom: 1.5rem;
}

.issue-number {
  font-family: 'Bangers', cursive;
  font-size: 2rem;
  color: #FFD700;
  display: inline-block;
  background: #000;
  border: 3px solid #FFD700;
  padding: 0.5rem 1rem;
  margin-bottom: 1rem;
}

.issue-title {
  font-family: 'Bangers', cursive;
  font-size: 2rem;
  color: #FF0044;
  margin-bottom: 0.5rem;
  text-shadow: 2px 2px 0 #00E5FF;
}

.issue-subtitle {
  font-family: 'Permanent Marker', cursive;
  font-size: 1.3rem;
  color: #00E5FF;
  margin-bottom: 0.5rem;
}

.issue-date {
  font-size: 1rem;
  color: #FFD700;
  opacity: 0.8;
}

.dialogue-bubble {
  background: rgba(255, 255, 255, 0.05);
  border: 3px solid #00E5FF;
  border-radius: 20px;
  padding: 1.5rem;
  position: relative;
  backdrop-filter: blur(10px);
}

.dialogue-bubble p {
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 0.8rem;
  color: #fff;
}

.dialogue-bubble p:last-child {
  margin-bottom: 0;
}

/* Projects Section */
.projects-section {
  position: relative;
  z-index: 10;
  width: 100%;
  overflow-y: auto;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
}

.project-panel {
  position: relative;
  background: #000;
  border: 5px solid #FFD700;
  padding: 2rem;
  transform: rotate(-2deg);
  box-shadow: 
    0 0 25px rgba(255, 215, 0, 0.3),
    8px 8px 0 rgba(255, 0, 68, 0.4);
  transition: transform 0.3s, box-shadow 0.3s;
  overflow: hidden;
}

.project-panel:hover {
  transform: rotate(2deg) translateY(-10px);
  box-shadow: 
    0 0 40px rgba(255, 215, 0, 0.5),
    12px 12px 0 rgba(255, 0, 68, 0.6);
}

.project-panel:nth-child(even) {
  transform: rotate(2deg);
  border-color: #00E5FF;
}

.project-panel:nth-child(even):hover {
  transform: rotate(-2deg) translateY(-10px);
}

.panel-border {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 2px dashed rgba(255, 255, 255, 0.2);
  pointer-events: none;
}

.project-title {
  font-family: 'Bangers', cursive;
  font-size: 1.8rem;
  color: #FF0044;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 0 #00E5FF;
}

.project-desc {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  color: #fff;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tags span {
  background: #00E5FF;
  color: #000;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  border: 2px solid #000;
  box-shadow: 2px 2px 0 #FFD700;
}

/* Contact Section */
.contact-section {
  position: relative;
  z-index: 10;
  width: 100%;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
}

.contact-card {
  background: #000;
  border: 4px solid #FF0044;
  padding: 2rem;
  text-align: center;
  transform: rotate(-2deg);
  box-shadow: 
    0 0 20px rgba(255, 0, 68, 0.3),
    5px 5px 0 rgba(0, 229, 255, 0.5);
  transition: transform 0.3s, box-shadow 0.3s;
}

.contact-card:hover {
  transform: rotate(2deg) scale(1.05);
  box-shadow: 
    0 0 30px rgba(255, 0, 68, 0.5),
    8px 8px 0 rgba(0, 229, 255, 0.7);
}

.contact-card:nth-child(even) {
  transform: rotate(2deg);
  border-color: #00E5FF;
}

.contact-card:nth-child(even):hover {
  transform: rotate(-2deg) scale(1.05);
}

.contact-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  filter: drop-shadow(0 0 10px rgba(255, 215, 0, 0.5));
}

.contact-label {
  font-family: 'Permanent Marker', cursive;
  font-size: 1.2rem;
  color: #FFD700;
  margin-bottom: 0.5rem;
}

.contact-value {
  font-size: 1rem;
  color: #fff;
  text-decoration: none;
  display: block;
  transition: color 0.3s;
}

.contact-value:hover {
  color: #00E5FF;
}

/* Footer */
.footer {
  position: relative;
  padding: 3rem 2rem;
  text-align: center;
  background: #000;
  border-top: 5px solid #FF0044;
  margin-top: 4rem;
}

.footer-burst {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgba(255, 215, 0, 0.3) 0%, transparent 70%);
  filter: blur(30px);
  animation: pulseBurst 2s ease-in-out infinite;
}

.footer-text {
  font-family: 'Permanent Marker', cursive;
  font-size: 1rem;
  color: #FFD700;
  letter-spacing: 0.05em;
}

/* Story Controls */
.story-controls {
  position: fixed;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 1rem;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.8);
  padding: 1rem;
  border: 3px solid #FF0044;
  border-radius: 50px;
  box-shadow: 0 0 30px rgba(255, 0, 68, 0.5);
  backdrop-filter: blur(10px);
}

.control-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #000;
  border: 3px solid #FFD700;
  color: #FFD700;
  font-size: 1.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.3);
}

.control-btn:hover {
  transform: scale(1.1);
  background: #FFD700;
  color: #000;
  box-shadow: 0 0 25px rgba(255, 215, 0, 0.6);
}

.play-btn.playing {
  border-color: #00E5FF;
  color: #00E5FF;
  box-shadow: 0 0 15px rgba(0, 229, 255, 0.3);
}

.play-btn.playing:hover {
  background: #00E5FF;
  color: #000;
}

/* Progress Dots */
.progress-dots {
  position: fixed;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 1000;
}

.progress-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  border: 2px solid #fff;
  cursor: pointer;
  transition: all 0.3s;
  padding: 0;
}

.progress-dot:hover {
  transform: scale(1.3);
  background: rgba(255, 215, 0, 0.6);
  border-color: #FFD700;
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.6);
}

.progress-dot.active {
  background: #FF0044;
  border-color: #FF0044;
  box-shadow: 0 0 20px rgba(255, 0, 68, 0.8);
  transform: scale(1.5);
}

/* Section Counter */
.section-counter {
  position: fixed;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  background: rgba(0, 0, 0, 0.9);
  border: 3px solid #00E5FF;
  padding: 0.5rem 1.5rem;
  border-radius: 30px;
  box-shadow: 0 0 20px rgba(0, 229, 255, 0.4);
}

.counter-text {
  font-family: 'Bangers', cursive;
  font-size: 1.3rem;
  color: #00E5FF;
  letter-spacing: 0.1em;
  text-shadow: 2px 2px 0 #FFD700;
}

/* Responsive - Tablet */
@media (max-width: 1024px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 1.2rem;
    padding: 1.5rem;
  }
  
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
    padding: 1.5rem;
  }

  .timeline {
    padding: 1.5rem;
  }

  .contact-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

/* Responsive - Mobile */
@media (max-width: 768px) {
  .story-section {
    padding: 1.5rem 1rem;
  }

  .glitch-title {
    font-size: clamp(2.5rem, 12vw, 4rem);
  }

  .role-text {
    font-size: clamp(1rem, 4vw, 1.5rem);
  }

  .comic-caption {
    font-size: 0.95rem;
    padding: 1rem;
  }

  .section-title {
    font-size: clamp(2rem, 8vw, 3rem);
    margin: 2rem 0 1.5rem;
  }
  
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
    gap: 0.8rem;
    padding: 1rem;
  }

  .skill-bubble {
    font-size: 0.85rem;
    padding: 0.8rem;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    padding: 1rem;
  }

  .project-panel {
    padding: 1.5rem;
  }

  .project-title {
    font-size: 1.5rem;
  }

  .project-desc {
    font-size: 0.9rem;
  }

  .timeline {
    padding: 1rem;
    gap: 2rem;
  }

  .comic-issue {
    padding: 1.5rem;
  }

  .issue-title {
    font-size: 1.5rem;
  }

  .issue-subtitle {
    font-size: 1.1rem;
  }

  .dialogue-bubble p {
    font-size: 0.95rem;
  }

  .narration-box {
    padding: 1.5rem;
  }

  .narration-header {
    font-size: 1.5rem;
  }

  .narration-text {
    font-size: 1rem;
  }
  
  .profile-card {
    width: 280px;
    height: 380px;
  }
  
  .sfx-text {
    font-size: 1.8rem;
  }
  
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    padding: 1rem;
  }

  .contact-card {
    padding: 1.5rem;
  }

  .story-controls {
    bottom: 20px;
    padding: 0.7rem;
    gap: 0.7rem;
  }

  .control-btn {
    width: 45px;
